import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import {t} from 'sn-translate';
import {measureTextFactory} from '@servicenow/library-enhanced-utils';
import focusBehavior from '@servicenow/behavior-focus';
import resizeBehavior from '@servicenow/behavior-resize';
import themeBehavior from '@servicenow/behavior-theme';
import '@servicenow/now-icon';
import '@servicenow/now-tooltip';
import {
	schedule,
	unschedule,
	createTooltipActionHandler
} from '@servicenow/now-tooltip';
import '@servicenow/now-dropdown';
import {actionTypes} from '@servicenow/ui-core';
import styles from './_now-breadcrumbs.scss';

const {COMPONENT_DISCONNECTED} = actionTypes;

const MIN_TRUNCATION_SIZE = 50;
const MAX_TRUNCATION_SIZE = 300;
const MIN_CONTAINER_SIZE = 160;

const createMeasureFn = (fn) => {
	return (text) => Math.ceil(fn(text));
};

const truncateItemText = (measureFn, label, size) => {
	const ellipse = 'â€¦';
	const ellipseSize = measureFn(ellipse);
	let displayLabel = label;

	while (measureFn(displayLabel) + ellipseSize > size) {
		displayLabel = displayLabel.slice(0, -1);
	}

	return `${displayLabel}${ellipse}`;
};

const processCrumbs = (items, themeables) => {
	const crumbs = [];
	let size = 0;

	items.forEach((item, i) => {
		const measureFn =
			i === items.length - 1
				? themeables.measure600Text
				: themeables.measure400Text;
		const initialLabelSize =
			item.hideLabel && item.icon ? 0 : measureFn(item.label);

		const truncate = initialLabelSize > MAX_TRUNCATION_SIZE;
		const displayLabel = truncate
			? truncateItemText(measureFn, item.label, MAX_TRUNCATION_SIZE)
			: item.label;
		const labelSize = truncate ? measureFn(displayLabel) : initialLabelSize;

		// label size
		size += Math.min(labelSize, MAX_TRUNCATION_SIZE);

		// icon size
		const iconPadding =
			item.hideLabel && item.icon ? 0 : themeables.iconLabelSpace;
		size += item.icon ? themeables.crumbIconSize + iconPadding : 0;

		const c = {
			label: item.label,
			href: item.href,
			icon: item.icon,
			hideLabel: item.hideLabel,
			original: item,
			id: i,
			truncate,
			displayLabel,
			labelSize
		};
		crumbs.push(c);
	});

	// Add in delimiter sizing
	size +=
		(crumbs.length - 1) *
		(themeables.delimitterIconSize + themeables.delimitterMargins);

	return [crumbs, size];
};

const truncateItem = (item, breadcrumbSize, containerSize, measureFn) => {
	if (item.labelSize <= MIN_TRUNCATION_SIZE) {
		return [item, breadcrumbSize];
	}

	breadcrumbSize -= item.labelSize;

	const targetSize =
		breadcrumbSize + MIN_TRUNCATION_SIZE > containerSize
			? MIN_TRUNCATION_SIZE
			: containerSize - breadcrumbSize;

	item.displayLabel = truncateItemText(
		measureFn,
		item.displayLabel,
		targetSize
	);
	item.labelSize = measureFn(item.displayLabel);
	item.truncate = true;

	breadcrumbSize += item.labelSize;

	return [item, breadcrumbSize];
};

const truncateItems = (crumbs, breadcrumbSize, containerSize, themeables) => {
	// first and last item get handled later if necessary
	for (let i = 1; i < crumbs.length - 1; i++) {
		if (breadcrumbSize <= containerSize) break;

		[crumbs[i], breadcrumbSize] = truncateItem(
			crumbs[i],
			breadcrumbSize,
			containerSize,
			themeables.measure400Text
		);
	}

	return [crumbs, breadcrumbSize];
};

const truncateFirstItem = (item, breadcrumbSize, containerSize, themeables) => {
	return truncateItem(
		item,
		breadcrumbSize,
		containerSize,
		themeables.measure400Text
	);
};

const truncateLastItem = (item, breadcrumbSize, containerSize, themeables) => {
	return truncateItem(
		item,
		breadcrumbSize,
		containerSize,
		themeables.measure600Text
	);
};

const calcDropdownItems = (
	crumbs,
	breadcrumbSize,
	containerSize,
	themeables
) => {
	const newCrumbs = [];
	const dropdownItems = [];
	const lastIndex = crumbs.length - 1;

	// always add first item
	newCrumbs.push(crumbs[0]);

	// add the size for the dropdown
	breadcrumbSize += themeables.dropdownButton;

	for (let i = 1; i < lastIndex; i++) {
		const item = crumbs[i];

		if (breadcrumbSize > containerSize) {
			dropdownItems.push(item);
			breadcrumbSize -= item.labelSize;
		} else {
			newCrumbs.push(item);
		}
	}

	// always add last item
	newCrumbs.push(crumbs[lastIndex]);

	return [newCrumbs, breadcrumbSize, dropdownItems];
};

const calcCrumbs = ({items, overflow}, {width}, themeables) => {
	// First, calc initial display label and size of everything
	let [crumbs, breadcrumbSize] = processCrumbs(items, themeables);
	let dropdownItems = null;
	const containerWidth = Math.max(width, MIN_CONTAINER_SIZE);

	// if <= container size or we're wrapping, we're done
	if (
		!containerWidth ||
		breadcrumbSize <= containerWidth ||
		overflow === 'wrap'
	) {
		return [crumbs, dropdownItems];
	}

	// do item truncation
	if (overflow === 'truncate-collapse') {
		[crumbs, breadcrumbSize] = truncateItems(
			crumbs,
			breadcrumbSize,
			containerWidth,
			themeables
		);
	}

	// collapse stuff into dropdown
	if (breadcrumbSize > containerWidth) {
		[crumbs, breadcrumbSize, dropdownItems] = calcDropdownItems(
			crumbs,
			breadcrumbSize,
			containerWidth,
			themeables
		);
	}

	// truncate the first item
	if (breadcrumbSize > containerWidth && overflow === 'truncate-collapse') {
		[crumbs[0], breadcrumbSize] = truncateFirstItem(
			crumbs[0],
			breadcrumbSize,
			containerWidth,
			themeables
		);
	}

	// truncate the current item
	if (breadcrumbSize > containerWidth && overflow === 'truncate-collapse') {
		const index = crumbs.length - 1;
		[crumbs[index], breadcrumbSize] = truncateLastItem(
			crumbs[index],
			breadcrumbSize,
			containerWidth,
			themeables
		);
	}

	return [crumbs, dropdownItems];
};

const renderIcon = (icon) => {
	if (!icon) return;

	return (
		<now-icon
			className="now-m-inline-end--xs"
			icon={icon}
			size="sm"
			aria-hidden="true"
		/>
	);
};

const showTooltip = (updateState, item, target) => {
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: target, content: item.label, opened: true}
	});
};

const hideTooltip = (updateState) => {
	updateState({
		path: 'currentTooltip',
		operation: 'set',
		value: {ref: null, content: '', opened: false}
	});
};

const renderItemLabel = (item) => {
	if (item.hideLabel && item.icon) return;

	return (
		<span className="now-line-height-crop">
			<span className="now-breadcrumbs-item-label" aria-hidden="true">
				{item.displayLabel}
			</span>
		</span>
	);
};

const renderDelimiter = (type, overflow) => {
	const icon = `${type}-end-fill`;
	return (
		<now-icon
			class={{
				'now-align': true,
				'-block-center': true,
				'now-m-inline--md': true,
				'now-m-block-end--sm': overflow === 'wrap'
			}}
			icon={icon}
			size="sm"
			aria-hidden="true"
			data-test="delimiter"
		/>
	);
};

const renderCrumb = (item, overflow, {dispatch, updateState}, componentId) => {
	// FIXME overflow should get removed when flex gap is supported
	return (
		<a
			class={{
				'now-breadcrumbs-item': true,
				'-link': true,
				'will-wrap': overflow === 'wrap'
			}}
			on-click={() => {
				dispatch(() => {
					return {
						type: 'NOW_BREADCRUMBS#ITEM_CLICKED',
						payload: {
							item: item.original
						}
					};
				});
			}}
			on-mouseenter={
				item.truncate || item.hideLabel
					? (e) =>
							schedule(
								componentId,
								showTooltip.bind(null, updateState, item, e.target)
							)
					: null
			}
			on-focusin={
				item.truncate || item.hideLabel
					? (e) =>
							schedule(
								componentId,
								showTooltip.bind(null, updateState, item, e.target)
							)
					: null
			}
			on-mouseleave={
				item.truncate || item.hideLabel
					? () => schedule(componentId, hideTooltip.bind(null, updateState))
					: null
			}
			on-focusout={
				item.truncate || item.hideLabel
					? () => schedule(componentId, hideTooltip.bind(null, updateState))
					: null
			}
			href={item.href}
			aria-label={item.label}>
			{renderIcon(item.icon)}
			{renderItemLabel(item)}
		</a>
	);
};

const renderDropdown = (items) => {
	return (
		<now-dropdown
			config-aria={{trigger: {'aria-label': t('Show more breadcrumb links')}}}
			className="now-breadcrumbs-dropdown"
			bare
			hide-caret
			hide-label
			size="md"
			icon="ellipsis-h-fill"
			variant="tertiary"
			items={items}
			manage-selected-items
		/>
	);
};

const renderCurrentItem = (item, overflow, {updateState}, componentId) => {
	// FIXME overflow should get removed when flex gap is supported
	// aria-current="location" is better than "page" per discussion in a11y slack and https://www.aditus.io/aria/aria-current/ and https://tink.uk/using-the-aria-current-attribute/
	return (
		<span
			class={{
				'now-breadcrumbs-item': true,
				'is-current': true,
				'will-wrap': overflow === 'wrap'
			}}
			aria-label={item.label}
			aria-current="location"
			on-mouseenter={
				item.truncate || item.hideLabel
					? (e) => {
							schedule(
								componentId,
								showTooltip.bind(null, updateState, item, e.target)
							);
					  }
					: null
			}
			on-focusin={
				item.truncate || item.hideLabel
					? (e) =>
							schedule(
								componentId,
								showTooltip.bind(null, updateState, item, e.target)
							)
					: null
			}
			on-mouseleave={
				item.truncate || item.hideLabel
					? () => schedule(componentId, hideTooltip.bind(null, updateState))
					: null
			}
			on-focusout={
				item.truncate || item.hideLabel
					? () => schedule(componentId, hideTooltip.bind(null, updateState))
					: null
			}>
			{renderIcon(item.icon)}
			{renderItemLabel(item)}
		</span>
	);
};

const renderBreadcrumbs = (state, containerSize, helpers) => {
	const {overflow, delimiter} = state.properties;
	const themeables = setThemeables(state.behaviors.theme);
	const dom = [];
	const [crumbs, dropdownItems] = calcCrumbs(
		state.properties,
		containerSize,
		themeables
	);
	const lastIndex = crumbs.length - 1;

	// always draw the first item unless it is the current item
	if (lastIndex > 0) {
		dom.push(renderCrumb(crumbs[0], overflow, helpers, state.componentId));
		dom.push(renderDelimiter(delimiter, overflow));
	}

	if (dropdownItems) {
		dom.push(renderDropdown(dropdownItems));
		dom.push(renderDelimiter(delimiter, overflow));
	}

	for (let i = 1; i < lastIndex; i++) {
		dom.push(renderCrumb(crumbs[i], overflow, helpers, state.componentId));
		dom.push(renderDelimiter(delimiter, overflow));
	}

	dom.push(
		renderCurrentItem(crumbs[lastIndex], overflow, helpers, state.componentId)
	);

	return dom;
};

const setThemeables = ({global, type, custom}) => {
	return {
		crumbIconSize: global.fontSize.sm.px,
		delimitterIconSize: global.fontSize.sm.px,
		delimitterMargins: global.space.md.px * 2,
		iconLabelSpace: global.space.xs.px,
		dropdownButton: global.fontSize.md.px * 2 * custom.button.heightScale,
		measure400Text: createMeasureFn(
			measureTextFactory(`400 ${global.fontSize.sm.px}px ${type.fontFamily}`)
		),
		measure600Text: createMeasureFn(
			measureTextFactory(`600 ${global.fontSize.sm.px}px ${type.fontFamily}`)
		)
	};
};

const view = (state, helpers) => {
	const {overflow} = state.properties;
	const containerSize = state.behaviors.resize.size;

	return (
		<nav
			class={{
				'now-breadcrumbs': true,
				'will-wrap': overflow === 'wrap'
			}}
			aria-label={t('Breadcrumb')}>
			<now-tooltip
				id="tooltip"
				manage-opened
				opened={state.currentTooltip.opened}
				target-ref={state.currentTooltip.ref}
				content={state.currentTooltip.content}
				delay={{show: 0, hide: 0}}
			/>
			{renderBreadcrumbs(state, containerSize, helpers)}
		</nav>
	);
};

/**
 * Creates a set of breadcrumbs which allows users to keep track and maintain awareness of their locations within the hierarchical structure of a product application. It provides the user a digital trail from their starting point to the current loaded content/page and the ability to navigate to each link.
 *
 * ```jsx
 * <now-breadcrumbs
 *  items={[
 *    {label: 'Home', href: '#'},
 *    {label: 'Activity Center', href: '#'},
 *    {label: 'REQ002100', href: '#'},
 *    {label: 'Service Article', href: '#'}
 *  ]}
 * />
 * ```
 *
 * @seismicElement now-breadcrumbs
 * @summary Creates a set of breadcrumbs which allows users to keep track and maintain awareness of their locations.
 * @uib.label Breadcrumbs
 * @uib.icon breadcrumbs
 * @uib.description Creates a set of breadcrumbs which allows users to keep track and maintain awareness of their locations.
 * @uib.category primitives
 * @uib.associatedTypes global.core
 * @uib.properties items, delimiter, overflow
 * @uib.actions NOW_BREADCRUMBS#ITEM_CLICKED
 */
createEnhancedElement('now-breadcrumbs', {
	properties: {
		/**
		 * List of breadcrumbs to display, in sorted order, from the first page to the current page.
		 * @type {Array.<{label: string, href: string, icon: string, hideLabel: boolean}>}
		 * @uib.required
		 * @uib.description List of items to display
		 * @uib.defaultValue [{"label": "First Item", "href": "#", "icon": "home-outline", "hideLabel": true}, {"label": "Breadcrumbs1", "href": "#"}, {"label": "Breadcrumbs2", "href": "#"}, {"label": "Breadcrumbs3"}]
		 * @uib.typeMetadata.emptyStateBehavior EMPTY_ARRAY
		 */
		items: {
			required: true,
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string', translatable: true},
						href: {type: 'string'},
						icon: {type: 'string'},
						hideLabel: {type: 'boolean'}
					},
					required: ['label']
				}
			}
		},
		/**
		 * Defines the type of overflow behavior.
		 * - `collapse` will collapse items into a dropdown when the breadcrumb is too large for its container
		 * and cannot remain one line.
		 * - `truncate-collapse` first will truncate text down to a minimum size, then collapse into a dropdown.
		 * - `wrap` will cause the breadcrumb to wrap to multiple lines.
		 * @type {('collapse'|'truncate-collapse'|'wrap')}
		 * @uib.label Overflow
		 * @uib.description Defines the overflow behavior.
		 */
		overflow: {
			default: 'collapse',
			schema: {
				type: 'string',
				enum: ['collapse', 'truncate-collapse', 'wrap']
			}
		},
		/**
		 * Defines the symbol to use as a separator between breadcrumb items.
		 * @type {('chevron'|'arrow')}
		 * @uib.label Delimiter
		 * @uib.description Defines the symbol to use as a separator between breadcrumb items.
		 */
		delimiter: {
			default: 'chevron',
			schema: {type: 'string', enum: ['chevron', 'arrow']}
		}
	},

	initialState: {
		currentTooltip: {ref: null, content: '', opened: false}
	},

	behaviors: [
		{behavior: focusBehavior},
		{
			behavior: resizeBehavior,
			options: {timer: {type: 'throttle', wait: 30}}
		},
		{
			behavior: themeBehavior,
			options: {
				items: [
					'type.fontFamily',
					'global.fontSize.sm',
					'global.fontSize.md',
					'global.space.xs',
					'global.space.md'
				],
				custom: [
					{
						name: 'button.heightScale',
						source: 'button--height-scale',
						initial: 1,
						schema: 'number'
					}
				]
			}
		}
	],
	actionHandlers: {
		...createTooltipActionHandler(hideTooltip),
		[COMPONENT_DISCONNECTED]: ({action}) => {
			const componentId = action.meta.id;
			unschedule(componentId);
		},
		'NOW_DROPDOWN#ITEM_CLICKED': ({action, dispatch}) => {
			dispatch(() => ({
				type: 'NOW_BREADCRUMBS#ITEM_CLICKED',
				payload: {
					item: action.payload.item.original
				}
			}));
		}
	},
	styles,
	view,
	dispatches: {
		/**
		 * Action dispatched when a breadcrumb is selected. The payload contains
		 * the item from the `items` array that was selected by the user.
		 * @type {{item: {label: string, href: string, icon: string, hideLabel: boolean}}}
		 * @uib.description Dispatched when the user clicks on a breadcrumb item (or item in the dropdown)
		 */
		'NOW_BREADCRUMBS#ITEM_CLICKED': {
			schema: {
				type: 'object',
				properties: {
					item: {
						type: 'object',
						properties: {
							label: {type: 'string'},
							href: {type: 'string'},
							icon: {type: 'string'},
							hideLabel: {type: 'boolean'}
						}
					}
				}
			}
		}
	}
});
